import { Component, Host, Listen, Prop, State, Watch, h } from '@stencil/core';
import { ApiCart } from '../../../../../utils/api/ecommerce/cart.api';
import { ApiProduct } from '../../../../../utils/api/ecommerce/product.api';
import { ORDERBY } from '../../../../../utils/api/ecommerce/utils/constants/requests.constant';
import { CartItem } from '../../../../../utils/api/ecommerce/utils/interfaces/cart.interface';
import { Product } from '../../../../../utils/api/ecommerce/utils/interfaces/product.interface';
import { OrderedParams } from '../../../../../utils/api/ecommerce/utils/interfaces/requests.interface';
import { currency } from '../../../../../utils/filters/currency.filter';
import { TP_PRODUCT_LIST } from './utils/product-list-constant';

@Component({
  tag: 'tp-elem-product-list',
  styleUrl: 'product-list.scss',
  scoped: true,
})
export class TpElemProductList {
  /**
   * O ID da categoria de produtos a serem listadas. Se nenhuma categoria
   * for definida, todos os produtos serão listados.
   */
  @Prop() categoryId: number | null;

  /**
   * Um conjunto de objetos com todos os dados dos produtos à mostra.
   */
  @State() products: Product[];

  /**
   * O total de páginas de produtos
   */
  @State() totalPages: number;

  /**
   * Um conjunto de objetos com todos os dados dos produtos à mostra.
   */
  @State() cart: CartItem[] = [];

  /**
   * O total de páginas de produtos
   */
  @State() filter: OrderedParams = {
    orderby: null,
    order: null,
    page: 1,
  };

  /**
   * O total de páginas de produtos
   */
  @State() loadingProducts: boolean;

  /**
   * Objeto de configuração do select popover
   */
  selectInterfaceOptions = { cssClass: 'tp-popover-select' };

  @Watch('filter')
  onUpdatedFilter() {
    const { categoryId, filter } = this;
    const params = categoryId ? { ...filter, category: categoryId } : filter;

    this.updateProducts(params);
  }

  @Listen('ionChange')
  onChangeFilter(event: CustomEvent) {
    if (!this.filter.orderby && event.detail.value === ORDERBY.DATE) return;

    switch (event.detail.value) {
      case ORDERBY.DATE:
        this.filter = {
          orderby: null,
          order: null,
          page: 1,
        };
        break;

      case ORDERBY.POPULARITY:
        this.filter = {
          orderby: 'popularity',
          order: null,
          page: 1,
        };
        break;

      case ORDERBY.PRICE:
        this.filter = {
          orderby: 'price',
          order: null,
          page: 1,
        };
        break;

      case ORDERBY.PRICE_ASC:
        this.filter = {
          orderby: 'price',
          order: 'asc',
          page: 1,
        };
        break;

      case ORDERBY.RATING:
        this.filter = {
          orderby: 'rating',
          order: null,
          page: 1,
        };
        break;

      case ORDERBY.TITLE:
        this.filter = {
          orderby: 'title',
          order: null,
          page: 1,
        };
        break;
    }
  }

  @Listen('tpNavigate')
  onNavigateToPage(event: CustomEvent) {
    const page = event.detail;

    this.filter = { ...this.filter, page };
  }

  @Listen('tpPrevious')
  onGoToPreviousPage() {
    const currentPage = this.filter.page;
    const page = currentPage - 1;

    this.filter = { ...this.filter, page };
  }

  @Listen('tpNext')
  onGoToNextPage() {
    const currentPage = this.filter.page;
    const page = currentPage + 1;

    this.filter = { ...this.filter, page };
  }

  @Listen('tpPageJump')
  onPageJump(event: CustomEvent) {
    const currentPage = this.filter.page;
    const direction = event.detail;
    let page = currentPage;

    if (direction === TP_PRODUCT_LIST.DIRECTIONS.FORWARD) {
      page =
        currentPage + TP_PRODUCT_LIST.PAGE_JUMP_AMOUNT > this.totalPages
          ? this.totalPages
          : currentPage + TP_PRODUCT_LIST.PAGE_JUMP_AMOUNT;
    } else {
      page =
        currentPage - TP_PRODUCT_LIST.PAGE_JUMP_AMOUNT < 1
          ? 1
          : currentPage - TP_PRODUCT_LIST.PAGE_JUMP_AMOUNT;
    }

    this.filter = { ...this.filter, page };
  }

  async componentWillLoad() {
    const params = this.categoryId ? { category: this.categoryId } : {};

    this.updateProducts(params);
    this.getCurrentCart();
  }

  async onAddToCart(productId: number) {
    try {
      const { success, cart } = await ApiCart.addToCart(productId, 1);
      if (success) this.cart = cart;
    } catch (error) {
      console.log('Não foi possível adicionar item ao carrinho.');
    }
  }

  async onRemoveFromCart(id: number) {
    try {
      const { success, cart } = await ApiCart.removeFromCart(id);
      if (success) this.cart = cart;
    } catch (error) {
      console.log('Não foi possível remover o item do carrinho.');
    }
  }

  async updateProducts(params: OrderedParams = {}) {
    this.loadingProducts = true;

    const { products, totalPages } =
      await ApiProduct.getOrderedProducts(params);

    this.products = products;
    if (totalPages !== this.totalPages) this.totalPages = totalPages;

    this.loadingProducts = false;
  }

  async getCurrentCart() {
    try {
      const { cart } = await ApiCart.getCart();
      this.cart = cart;
    } catch (error) {
      console.log('Não foi possível ter acesso ao carrinho.');
    }
  }

  render() {
    const {
      products,
      totalPages,
      filter: { page },
      loadingProducts,
      selectInterfaceOptions,
    } = this;

    const productItems = loadingProducts
      ? Array.from({ length: TP_PRODUCT_LIST.DEFAULT_PRODUCT_PER_PAGE }).map(
          () => (
            <ion-skeleton-text
              animated
              tp-component="tp-elem-product-item"
            ></ion-skeleton-text>
          ),
        )
      : products.map((product) => {
          const {
            permalink,
            id,
            name,
            regularPrice,
            price,
            stockQuantity,
            manageStock,
            stockStatus,
          } = product;

          const alreadyOnCart = this.cart.some((item) => item.id === id);
          const haveInStock =
            (manageStock && stockQuantity >= 1) ||
            (!manageStock && stockStatus === 'instock');

          return (
            <tp-elem-product-item product={product}>
              <ion-text slot="title" tp-type="p18xb" color="neutral-10">
                <h3>
                  <a href={permalink}>{name}</a>
                </h3>
              </ion-text>

              <ion-text slot="regular-price" color="neutral-6" tp-type="p14">
                <p>{currency(regularPrice, 'pt-br')}</p>
              </ion-text>

              <ion-text slot="price" color="neutral-8">
                <p>{currency(price, 'pt-br')}</p>
              </ion-text>

              {haveInStock && !alreadyOnCart && (
                <ion-button
                  slot="button"
                  color="neutral-2"
                  expand="block"
                  onClick={() => this.onAddToCart(id)}
                >
                  Adicionar ao Carrinho
                </ion-button>
              )}

              {haveInStock && alreadyOnCart && (
                <ion-button
                  slot="button"
                  color="warning"
                  expand="block"
                  onClick={() => this.onRemoveFromCart(id)}
                >
                  Remover do Carrinho
                </ion-button>
              )}
            </tp-elem-product-item>
          );
        });

    return (
      <Host>
        <div class="product-list">
          <div class="product-list__top">
            <tp-input-container class="product-list__filter">
              <ion-select
                interface="popover"
                interfaceOptions={selectInterfaceOptions}
                placeholder="Filtrar"
              >
                <ion-select-option value="date">Padrão</ion-select-option>
                <ion-select-option value="popularity">
                  Classificar por popularidade
                </ion-select-option>
                <ion-select-option value="rating">
                  Classificar por avaliação
                </ion-select-option>
                <ion-select-option value="price-asc">
                  Classificar por menor preço
                </ion-select-option>
                <ion-select-option value="price">
                  Classificar por maior preço
                </ion-select-option>
              </ion-select>
            </tp-input-container>
          </div>

          <div class="product-list__middle">{productItems}</div>

          {totalPages > 1 && (
            <div class="product-list__bottom">
              <tp-pagination
                pageCount={totalPages}
                currentPage={page}
              ></tp-pagination>
            </div>
          )}
        </div>
      </Host>
    );
  }
}
